.page-container {
    display: flex;
    justify-content: flex-end;
    border: 1px solid var(--default-grey-color);
    border-radius: 5px;
    overflow: hidden;
}

.page-item {
    cursor: pointer;
    position: relative;
    padding: 6px 10px;
    color: var(--main-color);
    height: 100%;
    min-width: 2.8rem;
    text-align: center;
}

.page-item:hover {
    background-color: var(--light-grey-color);
}

.page-item::after {
    position: absolute;
    content: "";
    display: block;
    width: 1px;
    height: 100%;
    background-color: var(--default-grey-color);
    top: 0;
    right: 0;
}

.page-item:last-child::after {
    display: none;
}

.blocked {
    pointer-events: none;
    cursor: default;
    color: var(--default-grey-color);
}

.current-page {
    pointer-events: none;
    cursor: default;
    color: white;
    background-color: var(--main-color);
}

.current-page:hover {
    color: white;
    background-color: var(--main-color);
}